.header {
    &-block {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 80px; }
    &-label {
        color: #fff;
        margin: 0;
        &:hover {
            color: #3b4b54; } }
    &-link {
        display: flex;
        margin: 0;
        align-items: center;
        color: #fff;
        list-style-type: none;
        li {
            margin-right: 20px;
            font-size: 18px;
            &:hover {
                color: #3b4b54; } } } }

